	module.exports = {"data":{"file":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.9657118055555556,"src":"/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg","srcSet":"/static/avatar-820706df6fdb07a9b021a790449fec17-62f05.jpg 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-dc2b4.jpg 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-e5d70.jpg 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-d14e0.jpg 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9cc71.jpg 2225w","srcWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp","srcSetWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-5eed2.webp 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-68e3c.webp 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-300fe.webp 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-ed5bd.webp 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9442a.webp 2225w","sizes":"(max-width: 800px) 100vw, 800px"}}},"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"I am Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}},"markdownRemark":{"html":"<p>You won’t rush an architect. You don’t want to see your house becoming dust at the next earthquake. </p>\n<p>You won’t rush your hairdresser. You don’t want to leave that place with one ear.</p>\n<p>You won’t rush your dentist. You can’t handle the pain.</p>\n<p>You won’t rush your local restaurant chef. You know good beef takes time to cook.</p>\n<p>You won’t rush your wife to deliver your baby in less than 9 months. Why would you?</p>\n<p>But you will rush your developer to build the next Facebook in 48 hours.</p>\n<p>It makes sense.</p>","excerpt":"You won’t rush an architect. You don’t want to see your house becoming dust at the next earthquake.  You won’t rush your hairdresser. You…","frontmatter":{"date":"July 15, 2015","path":"/it-makes-sense/","tags":null,"title":"It makes sense","draft":null}}},"pathContext":{"prev":{"excerpt":"Making a discoverable API means thats some software would be able to traverse it, by only providing it's root endpoint. For example, my IDE could provide me all the endpoints, input schemas and responses of Github's API by putting in it's root, that…","html":"<p>Making a discoverable API means thats some software would be able to traverse it, by only providing it's root endpoint. For example, my IDE could provide me all the endpoints, input schemas and responses of Github's API by putting in it's root, that is <code>https://api.github.com</code>.</p>\n<p>This can have some major advantages. Speed would be the absolute greatest, since I don't have to search through documentation and going back and forth their site. Also, in case of an endpoint changes it's name, the software will automatically adopt the new name.</p>\n<p>Now, all these are definitely good, but we don't see that much of API's making themselves discoverable. Why is that? Well, mostly because speed is not that much of an issue, since REST is pretty explanatory it self, you only have a few verbs to respond to, so each API's documentation wouldn't be that much. As for the breaking changes, well putting a Version number in front of each endpoint seems to work pretty good.</p>\n<p>The first thing that comes in mind when talking about discovering web services is Web Services Description Language(WSDL). Before the major adoption of REST principles, people would create APIs in different forms. And so a way to describe them was necessary. That's where WSDL would take action. REST on the other hand is not the same. When we hear an API is REST compliant, we know that uses the common HTTP verbs, can return/accept XML or JSON and respond with the known HTTP statuses codes. That is what REST APIs is all about.</p>\n<p>Nevertheless, I would say that machines that talk to each other, that are indeed able to discover each other's resources, is a tasty subject and I'm pretty sure that we will see it really soon. At the mean time, let's focus on making our APIs usable and understandable with bare eye.</p>\n<p>Here are some references you can use if you want to make your API's discoverable. You can check out these great specs about describing your API through it's responses. I am not sure how these can be widely adopted, but once they do, we should expect it to go big. Never used them, so I am expecting from you. Google's also has looked into this and came out with some really fascinated results, be sure to check them also.</p>\n<p><a href=\"http://micro-api.org/\" target=\"_blank\" rel=\"noreferrer noopener\">http://micro-api.org/</a>\n<a href=\"http://jsonapi.org/\" target=\"_blank\" rel=\"noreferrer noopener\">http://jsonapi.org/</a>\n<a href=\"http://www.odata.org/\" target=\"_blank\" rel=\"noreferrer noopener\">http://www.odata.org/</a>\n<a href=\"https://developers.google.com/discovery/\" target=\"_blank\" rel=\"noreferrer noopener\">https://developers.google.com/discovery/</a></p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/making-a-rest-api-discoverable/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2015-08-24T14:39:27.000Z","path":"/making-a-rest-api-discoverable/","tags":"rest, api","title":"Making a REST API discoverable","draft":null}},"next":{"excerpt":"First, become the best in the room. Then find a room where everyone is better than you. Repeat. I know this piece is like 20 words long but could it be a spark for a great conversation? Leave your comment...","html":"<ul>\n<li>First, become the best in the room.</li>\n<li>Then find a room where everyone is better than you.</li>\n<li>Repeat.</li>\n</ul>\n<p>I know this piece is like 20 words long but could it be a spark for a great conversation?</p>\n<p>Leave your comment...</p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/are-you-the-best-in-the-room/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2015-07-11T15:07:57.000Z","path":"/are-you-the-best-in-the-room/","tags":null,"title":"Are you the best in the room?","draft":null}}}}

